@import url("https://fonts.googleapis.com/css2?family=Cinzel:wght@400..900&family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");
.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 100vw;
  width: 100%;
  align-items: center;
  height: 100vh;
  margin-top: 100px;
  border-radius: 12px;
  .container {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    box-shadow: 2px 2px 12px 0px #242f7b26;
    width: 100%;
    gap: 24px;
    padding: 12px;
    .title {
      font-family: "Cinzel", serif;
      font-weight: 200;
      font-size: 48px;
    }
    .inputBlock {
      display: flex;
      justify-content: center;
    }
    .tasks {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 800px;
      width: 100%;
      min-height: 100px;
      gap: 5px;
      margin-bottom: 50px;
      max-height: 400px;
      overflow-y: auto;
      .task {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 600px;
        width: 100%;
      }
    }
    .listLength {
      display: flex;
      padding-left: 12px;
      align-items: center;
      justify-content: space-between;
      .navigateList {
        display: flex;
        gap: 10px;
        li {
          display: flex;
          list-style: none;
        }
      }
    }
  }
  ::-webkit-scrollbar {
    width: 6px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 6px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.5);
  }

  ::-webkit-scrollbar-track {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 6px;
  }
}

@media screen and (max-width: 500px) {
  .listLength {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    ul {
      margin: 0;
      padding: 0;
      flex-wrap: wrap;
    }
  }
}
