.containerCheckBox {
  max-width: 24px;
  max-height: 24px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  .customCheckBox {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 24px;
    min-height: 24px;
    width: 100%;
    height: 100%;
    background: white;
    border: 1px solid rgb(105, 105, 105);
    border-radius: 50%;
  }
}
